/* Hide default cursor globally */
* {
  cursor: none !important;
}

/* Main cursor dot */
.custom-cursor {
  position: fixed;
  width: 8px;
  height: 8px;
  pointer-events: none;
  z-index: 9999;
  transform: translate(-50%, -50%);
  transition: all 0.1s ease;
  mix-blend-mode: difference; /* Creates cool invert effect */
}

.cursor-inner {
  width: 100%;
  height: 100%;
  background: var(--color-accent);
  border-radius: 50%;
  box-shadow: 0 0 8px var(--color-accent);
  animation: cursorPulse 2s ease-in-out infinite;
}

/* Outer ring */
.custom-cursor-ring {
  position: fixed;
  width: 32px;
  height: 32px;
  border: 2px solid var(--color-accent);
  border-radius: 50%;
  pointer-events: none;
  z-index: 9998;
  transform: translate(-50%, -50%);
  transition: all 0.15s ease;
  opacity: 0.6;
}

/* Hover states - cursor grows and glows */
.custom-cursor.hovering {
  transform: translate(-50%, -50%) scale(1.5);
}

.custom-cursor.hovering .cursor-inner {
  box-shadow: 0 0 16px var(--color-accent), 0 0 32px var(--color-accent);
  animation: cursorHoverPulse 0.8s ease-in-out infinite;
}

.custom-cursor-ring.hovering {
  transform: translate(-50%, -50%) scale(2);
  border-width: 1px;
  opacity: 0.8;
}

/* Pulse animations */
@keyframes cursorPulse {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.8;
  }
}

@keyframes cursorHoverPulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
}

